* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  /* background: url("/src/Assets/pietro-de-grandi-T7K4aEPoGGk-unsplash.jpg") !important; */
  /* background-image:url("https://media.istockphoto.com/photos/clouds-on-sky-picture-id184103864?b=1&k=20&m=184103864&s=170667a&w=0&h=ZngznsmOs4gnb0bRP13q5RFXY-cv-BHZJNjJXb6ffyk="); */
  background-image: url(/static/media/pietro-de-grandi-T7K4aEPoGGk-unsplash.07c6bd9e.jpg) !important;

  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  height: 100vh;
  font-family: "Poppins", sans-serif;
}

.appContainer {
  color: white;
  font-weight: bold;  
  background: rgba(24, 24, 24, 0.5);
  
  height: 100vh;
}
.space{
  padding: 20px 70px;
}

.time {
  font-size: 70px;
  font-weight: 100px;
}
.amPm {
  font-size: 30px;
  margin-left: 20px;
  font-weight: 100px;
}
.date {
  font-size: 30px;
}
.city {
  text-align: end;
  padding-top: 50px;
  font-size: 30px;
}
.daily {
  background: rgba(24, 24, 24, 0.5);
  padding: 30px;
  position: fixed;
  bottom: 0;
  display: flex;
  color: white;
  width: 100%; 
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
  left: 0;
}
.dayName {
  padding: 5px 15px;
  background: #142424;
  border-radius: 50px;
  text-align: center;
}
.dailyCard{
  border-radius: 10px;
  padding: 5px;
  margin:5px;
}
.today{
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #eee;
  border-radius: 10px;
  padding:15px;
  padding-right: 40px;
  border-radius: 10px;
  background: rgba(0,0,0,0.2);
 
}

.detailed{
   background: rgba(24, 24, 24, 0.5);
   border-radius: 10px;
   width: 100%;
   height:60vh;
   margin-top:50px
}
.detailed-box{
  padding-top: 30px;
  height:20vh

}
.search{
  width:40%;
  margin:auto;
  margin-top: 20px
}
.login{  
    background: rgba(24, 24, 24, 0.5);
    border-radius: 10px;
    width: 50%;
    margin:auto;
    height:60vh;
    margin-top:50px 
}
.input{
  border-radius:20px;
  background: transparent;
  border: 1px solid white;
  color: white;  
}

.form {  
  margin: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  font-size: 18px;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

.formContainer {
  display:flex;
  flex-direction: column;
  justify-content: space-around;
  width: 400px;
  height: 70vh;
  max-width: 400px;
  margin: 1rem;
  padding: 2rem;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.8);
  border-radius: 10px; 
  border: 1px soid white;
  /* background: transparent; */
  background-color: rgba(0, 0, 0, 0.8);
  /* background-color: white; */

}
input{ 
  background-color: rgba(243, 234, 234, 0.8);
  color: white;
  width: 90%;
  border-radius: 5px;
  margin:10px 0px;
  padding:10px 5px;
}

.btn{
  padding: 5px 5px;
  border-radius: 5px;
  background-color: rgba(243, 234, 234, 0.8);
  width: 30%;

}

.hidden{
  display: none;
}

.searchBtn{
  width: 35%;
  background: rgba(24, 23, 23, 0.8);
  color: white



}
